<template>
    <input type="text" v-model="school_name_input">
    <input type="button" @click="findSchoolByName" value='검색'>

    <select name="school_list_select" id="school_list_select" v-model="school_list_selected">
        <option value="" disabled selected>학교를 선택해주세요.</option>
        <option v-for="(a, i) in school_list" :key="i" :value="a">{{a.ORG_RDNMA + ' ' + a.SCHUL_NM}}</option>
    </select>

    <div v-if="school_list_selected != ''">
        <p>ATPT_OFCDC_SC_CODE (시도교육청코드) : {{school_list_selected.ATPT_OFCDC_SC_CODE}}</p>
        <p>SD_SCHUL_CODE (표준학교코드) : {{school_list_selected.SD_SCHUL_CODE}}</p>
        <p>SD_SCHUL (학교이름) : {{school_list_selected.SCHUL_NM}}</p>
        <p>{{TI_FROM_YMD_input}}</p>
        <p>{{TI_TO_YMD_input}}</p>
    </div>
    
    <div v-if="school_list_selected != ''">
        <input type="text" v-model="GRADE_input" placeholder="학년">
        <input type="text" v-model="CLASS_NM_input" placeholder="반명">
        <!-- <a @click="getSchedule">omg</a> -->
        <input type="button" @click="getSchedule" value="조회">
    </div>

    <tbody class="schedule_tbody">
        <td v-for="(a, i) in table" :key="i">
            <tr v-for="(b,j) in a" :key="j" :class="getClassStr(b.class,'')">
                <a :class="getClassStr(b.class)">{{b.value}}</a>
            </tr>
        </td>
    </tbody>
</template>
<script>
import $ from 'jquery';
export default {
    data() {
        return {
            school_list : null,
            school_name_input : '',
            school_list_selected : '',

            GRADE_input : '',
            TI_FROM_YMD_input : '',
            TI_TO_YMD_input : '',
            CLASS_NM_input : '',

            schedule : null,
            schedule_name_list : [],
            table : [],
        }
    },
    methods: {
        findSchoolByName : function(){
            var url = 'https://open.neis.go.kr/hub/schoolInfo?Type=json&key=&SCHUL_NM='+this.school_name_input;
            var result = null;
            $.ajax({
                url: url,
                type: 'get',
                dataType: 'json',
                async: false,
                success: function(data){
                    result = data['schoolInfo'][1]['row'];
                }
            });
            this.school_list = result
            console.log('result :',result);
            console.log('school_list :',this.school_list);
        },
        getSchedule: function(){
            var date = Date.now();
            var weekday = new Date().getDay()
            var mon = new Date(date - ((weekday - 1) * 1000 * 60 * 60 * 24));
            var fri = new Date(date - ((weekday - 5) * 1000 * 60 * 60 * 24));
            this.TI_FROM_YMD_input = String(mon.getFullYear()) + String(mon.getMonth()+1).padStart(2,'0') + String(mon.getDate()).padStart(2,'0');
            this.TI_TO_YMD_input = String(fri.getFullYear()) + String(fri.getMonth()+1).padStart(2,'0') + String(fri.getDate()).padStart(2,'0');
            var url = 'https://open.neis.go.kr/hub/misTimetable?key=13eca7ab97a346ebaf5575f3e82b043d&Type=json&ATPT_OFCDC_SC_CODE='+this.school_list_selected.ATPT_OFCDC_SC_CODE+'&SD_SCHUL_CODE='+this.school_list_selected.SD_SCHUL_CODE+'&GRADE='+this.GRADE_input+'&CLASS_NM='+this.CLASS_NM_input+'&TI_FROM_YMD='+this.TI_FROM_YMD_input+'&TI_TO_YMD='+this.TI_TO_YMD_input;
            console.log(url);
            var result = null;
            $.ajax({
                url: url,
                type: 'get',
                dataType: 'json',
                async: false,
                success: function(data){
                    result = data['misTimetable'][1]['row'];
                    if(data['misTimetable']['head']['RESULT']['CODE'] != "INFO-000")
                    console.error('api 호출에 실패하였습니다.');
                }
            });
            this.schedule = result;
            console.log('result :',result);
            console.log('schedule :', this.schedule);
            
            // data analyze
            console.log(result.map(x=>x['ALL_TI_YMD']));
            var schedule_date = result.map(x=>x['ALL_TI_YMD']);
            var schedule_class_length_list = [0,0,0,0,0]; // 요일 별 교시 개수
            var schedule_name_list = new Array(); 
            var buffer = schedule_date[0];
            var n = 0;
            for(var i=0;i<schedule_date.length;i++){
                if(schedule_date[i] != buffer){
                    n++
                    buffer = schedule_date[i];
                 }
                schedule_class_length_list[n]++
            }
            console.log(schedule_class_length_list);
            console.log(Math.max(...schedule_class_length_list));
            i = 0;
            for(var j=0;j<5;j++){
                schedule_name_list.push(new Array());
                for(var k=0;k<schedule_class_length_list[j];k++){
                    var sn = '';
                    if(result[i].ITRT_CNTNT.replace('-','') == '기술·가정'){
                        sn = '기가';
                    }else if(result[i].ITRT_CNTNT.replace('-','') == '생활 중국어'){
                        sn = '중국어';
                    }else if(result[i].ITRT_CNTNT.replace('-','') == '봉사활동'){
                        sn = '봉사';
                    }else if(result[i].ITRT_CNTNT.replace('-','') == '성교육자율활동'){
                        sn = '성교육';
                    }else if(result[i].ITRT_CNTNT.replace('-','') == '동아리활동'){
                        sn = '동아리';
                    }else if(result[i].ITRT_CNTNT.replace('-','') == '진로활동'){
                        sn = '진로';
                    }else{
                        sn = result[i].ITRT_CNTNT.replace('-','');
                    }
                    schedule_name_list[j].push(sn);
                    i++;
                }
            }
            for(i=0;i<5;i++){
                while(schedule_name_list[i].length != Math.max(...schedule_class_length_list)){
                    schedule_name_list[i].push('');
                }
            }
            console.log(schedule_name_list);
            
            // this.table = new Array(new Arrya * Math.max(...schedule_class_length_list));
            this.table = new Array();
            for(i=0;i<6;i++){
                this.table.push(new Array());
            }
            this.table[0][0] = {
                class: ['class_info','day_info'],
                value: '교시'
            };
            var weekdays = ['월', '화', '수', '목', '금'];
            for(i=0;i<5;i++){
                this.table[i+1].push({
                    class: ['day_info'],
                    value: weekdays[i]
                });
            }
            for(i=0;i<Math.max(...schedule_class_length_list);i++){
                this.table[0].push({
                    class: ['class_info'],
                    value: i+1
                });
                for(j=0;j<5;j++){
                    if(schedule_name_list[j][i] == ''){
                        this.table[j+1].push({
                        class: ['null_subject'],
                        value: '　'
                    });
                    }else{
                        this.table[j+1].push({
                            class: [schedule_name_list[j][i]],
                            value: schedule_name_list[j][i]
                        });
                    }
                }
            }
            console.log(this.table);
        },
        getClassStr: function(arr){
            var n = '';
            if(arguments.length == 2){
                n = '_tr';
            }
            return arr.join(n+' ')+n;
        },
    },
    computed:{
    },
    created(){
    }
}
</script>